@use '../base/mixins' as m;
@use '../base/variables' as v;


.redes-sociales{
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 5rem;
    padding: 0;
    width: 100%;

    &-q {
        display: none;
    }

}


.redes-sociales__titulo {

    margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 5rem;
        padding: 0.4rem 1.1rem;
       

    h2 {
        color: v.$verdeOscuro;
        font-family: v.$play;
        font-size: 3rem;
        text-transform: uppercase;
        margin: auto;
        margin-top: 2rem;
        margin-bottom: 2rem;
        text-align: center;
        
    }
}





.redes-sociales__cuerpo {
    
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
   width: 100%;

   @include m.tablet {
    display: flex;
   }

  
   
}




.GitHub-F {
       
    height: 10rem;
    justify-content: space-around;
    padding: 0 3rem;
    width: 100%;
    align-items: center;
    display: flex;
    position: relative;

    @include m.tablet {
        width: 50%;
    }

    
    
                svg {
                    font-size: 6rem;
                    margin-bottom: 2rem;

                
                }
    
                p{
                    font-size: 6rem;
                    padding: 0;
                    margin: 0;
                    color: v.$blanco;

                  

                  
                }
        
}






    .linkedin-F {


        height: 10rem;
        justify-content: space-around;
        padding: 0 3rem;
        width: 100%;
        align-items: center;
        display: flex;
        position: relative;
        background-color: #0e76a8;


        @include m.tablet {
            width: 50%;
        }
        
       
                    svg {
                        font-size: 6rem;
                    margin-bottom: 2rem;

                 
                    }
        
                    p{
                        font-size: 6rem;
                        padding: 0;
                        margin: 0;
                        color: v.$blanco;

                 
                      
                    }
            
    }

        

