@use '../base/mixins' as m;
@use '../base/variables' as v;



.proyecto-seccion {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 2rem;

    &-q {
        display: none;
    }
}

.proyecto-titulo {

    margin: 0 auto;
    border: 3px solid v.$verdeOscuro;
    width: 23rem;
    height: 7rem;
    border-radius: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 5rem;
        padding: 0.4rem 1.1rem;

        &-q {
            display: none;
        }

        @include m.desktop {
            padding: 0.8rem 2rem;

        }

    h2 {
        color: v.$verdeOscuro;
        font-family: v.$play;
        font-size: 3rem;
        text-transform: uppercase;
        margin: auto;
    }
}

.proyecto {
    align-items: center;
    display: flex;
    flex-direction: column;
    height: 70vh;
    justify-content: center;
    position: relative;
    width: 100%;

    &-q {
        display: none;
    }


    @include m.tablet {
        height: 80vh;
        
    }

    @include m.desktop {
        height: 90vh;
    }
}


.proyecto-background{

    background-image: url(../img/imagenB.jpg);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-filter: blur(8px);
    height: 80%;
    object-fit: contain;
    width: 100%;

    &-q{
        display: none;
    }

    @include m.tablet {
        height: 80vh;
    }

   
}

.slider {
    align-items: center;
    background-color: transparent;
    display: flex;
    height: 80%;
    justify-content: center;
    left: 50%;
    margin: 0;
    padding: 0;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    
    &-q {
        display: none;
    }
    


}

.left {
    height: 4rem;
    left: 2rem;
    top: 35%;
    width: 3rem;
    color: v.$negro;
    position: absolute;


    &:hover {
        cursor: pointer;
    }

    @include m.tablet {
        width: 5rem;
        top: 38%;
        left: 6rem;
        height: 8.8rem;
    }

    @include m.desktop {
        width: 9rem;
        top: 45%;
        left: 10rem;
        height: 8.8rem;
    }
    

}


.right {
    height: 4rem;
    right: 2rem;
    top: 35%;
    width: 3rem;
    color: v.$negro;
    position: absolute;


    &:hover {
        cursor: pointer;
    }

    @include m.tablet {
        width: 5rem;
        top: 38%;
        right: 3rem;
        height: 8.8rem;
    }

    @include m.desktop {
        width: 9rem;
        top: 45%;
        right: 6rem;
        height: 8.8rem;
    }
    
}


.slide {

    
    display: none;
   
    

  
}

.activo {
    align-items: center;
    background-color: v.$blanco;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    height: max-content;
    justify-content: center;
    position: absolute;
    right: auto;
    top: 15%;
    width: 24rem;


    @include m.tablet {
        right: 15%;
        top: 5%;
        width: 50rem;
    }

    @include m.desktopxl {
      
        right: 28%;
        top: 5%;
        width: 70rem;
        
    }



    .image {
        height: 13.6rem;
        width: 24rem;
        border-radius: 1rem;
        overflow-clip-margin: content-box;
        overflow: clip;

        @include m.tablet {
            height: 39.8rem;
            width: 70rem;
        }

        @include m.desktopxl {
            height: 39.8rem;
            width: 70rem;
        }
    }

    
}


.detalle {

   
    text-align: center;
    margin-bottom: 1rem;
    margin-top: 1rem;
    
   


    h4{
        font-family: v.$play;
        color: v.$verde;
        margin-bottom: 1rem;
       

        @include m.desktopxl {
            
            font-size: 1.8rem;
        }
       
    }


    span {
        
   

        a {
            background-color: v.$verdeOscuro;
            border: 2px solid v.$negro;
            border-radius: 15px;
            margin: 0;
            color: v.$blanco;
            font-weight: 700;
            text-transform: lowercase;
            padding: 0.1rem 0.6rem ;
            

        }
    }

}


