@use '../base/mixins' as m;
@use '../base/variables' as v;


.imagen-logo {

    width: 10rem;
    margin-left: 5rem;
    @include m.desktop {
        margin-left: 15rem;
    }

    &.quitar-l {
        display: none;
    }

   
}


.empanedado {

    width: 5rem;
    margin-right: 5rem ;

    @include m.desktop {
        margin-right: 15rem ;
    }

    &:hover {
        cursor: pointer;
    }


}

.header {


    background-color: v.$verde;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  


}




.quitar {
    display: none;
}








