@use '../base/variables' as v;
@use '../base/mixins' as m;


.tooling {
    padding: 5rem 0;

    &-q {
        display: none;
    }
}

.tooling-titulo {
    margin: 0 auto;
    border: 3px solid v.$verdeOscuro;
    width: 23rem;
    height: 7rem;
    border-radius: 1rem;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 5rem;

        &-q {
            display: none;
        }
    

    h2 {
        color: v.$verdeOscuro;
        font-family: v.$play;
        font-size: 3rem;
        text-transform: uppercase;
        margin: auto;
    }
}

.tooling-caja {
    margin: 0 auto;
    border: 3px solid black;
    background-color: v.$verdeOscuro;
    width: 85%;
    border-radius: 1.5rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 0.5rem;

    &-q {
        display: none;
    }

    @include m.tablet {
        width: 70%;
    }

    @include m.desktop {
        width: 50%;
    }

    .contenedor-box {
                border: 3px solid v.$verdeOscuro;
              display: flex;
              align-items: center;
                justify-content: center;
                gap: 1.5rem;
              width: 15rem;
              background-color: black;
              border-radius: 1.5rem;
    
     
        .box-text {
          
             p {
                 
                 font-size: 2rem;
                 font-family: v.$play;
                 color: v.$verdeOscuro;
                 
             }
 
         }
    }


}

