@use '../base/mixins' as m;
@use '../base/variables' as v;

.home {
    background-image: url(../img/imagen-fondo.jpg);
    background-position: center center;
    background-size: cover;

    display: grid;
    grid-template-rows: 80% 20%;
    margin: 0 auto;
    padding-top: 20rem;

    &-q {
        background-image: none;
        display: none;
    }


    @include m.tablet {
        display: grid;
        grid-template-rows: 50% 50%;
        padding-top: 10rem;

        &-q {
            background-image: none;
            display: none;
        }

    }
   


    @include m.desktop {
        display: grid;
        grid-template-rows: 100%;
        display: flex;

        justify-content: center;
        align-items: center;
        padding-bottom: 10rem;
        padding-left: 5rem;

        &-q {
            background-image: none;
            display: none;
        }

    }

 

}


span{
    font-family: v.$play;
    color: v.$verde;
}

.contenido-h {

    text-align: center;
    width: 90%;
    margin: 0 auto;

    @include m.desktop {
    
        margin: 0 auto;
    }


    &__hola {
        
        p {
            font-family: v.$play;
             color: v.$verde;
            
        }

        &-q{
            display: none;
        }
  
        
    }

    &__soy {
        
        h1 {
            font-family: v.$play;
            color: v.$blanco ;
            font-size: 5rem;
            font-weight: bolder;
        }

        &-q{
            display: none;
        }
       
    }

    &__p {

        
        p {
            line-height: 1.2;
            font-family: v.$play;
            color: v.$blanco ;
        }

        &-q{
            display: none;
        }
    }

}

.icono-yo {
    
    text-align: center;
    width: 90%;
    margin: 0 auto;

    &__img {
        
        @media (min-width: 500px) and (max-width:768px) {
            display: none;
        }
        
        
        @include m.desktop {
            margin-bottom: 50rem;
            margin: 0 auto;
        }

     
    }

    &-q{
        display: none;
    }

    
}

