@use '../base/mixins' as m;
@use '../base/variables' as v;


.imagen-logow {

    width: 10rem;
    margin-left: 5rem;
    @include m.desktop {
        margin-left: 15rem;
    }

    &-q {
        display: none;
    }
  

   
}


.empanedadow {

    width: 5rem;
    margin-right: 5rem ;

    @include m.desktop {
        margin-right: 15rem ;
    }

    &:hover {
        cursor: pointer;
    }

    &-q {
        display: none;
    }
  

}

.headerw {


    background-color: v.$verde;
    height: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

    &-q {
        display: none;
    }
  
  

}


.quitar {
    display: none;
}





