
@use '../base/mixins' as m;
@use '../base/variables' as v;


//Segunda ventana emergente



    .menu-secundario{

        display: none;
    
        &.mostrar-D{
            
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            display: grid;
            grid-template-rows: repeat(2,1fr);
           
            @include m.tablet {
                display: grid;
                grid-template-rows: 100%;
                grid-template-columns: 45% 55%;
            }
         
   
        }


        
    
    
    }

    
    
    .menu-largo {
    
        display: grid;
        grid-template-rows: 50% 50%;

        @include m.tablet {

            font-size: 7rem;
            display: grid;
            grid-template-rows:100%;
            grid-template-columns: 50% 50%;
           
        }
       
    
    }

    
 

    .GitHub {
           
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100%;

            .GitHub_box {
              
                color: v.$blanco;
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 3rem;

                @include m.tablet {
    
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    justify-content: center;
                    gap: 7rem;

                   
                }
            

                    svg {
                        font-size: 6rem;
                        margin-bottom: 2rem;

                        @include m.tablet {
                            transform: rotate(-90deg);
                        }
                        
                    }
        
                    p{
                        font-size: 6rem;
                        padding: 0;
                        margin: 0;

                        @include m.tablet {
                            font-size: 10rem;
                            transform: rotate(-90deg);
                          
                        }

                    }
            
            }
    }



    

        .linkedin {
            
            background-color: #0e76a8;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;


            &_box{
                    color: v.$blanco;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 3rem;
                   
                    @include m.tablet {
    
                        display: flex;
                        flex-direction: column;
                        align-items: center;
                        justify-content: center;
                        gap: 10rem;
    
                       
                    }
                

                    svg {
                        font-size: 6rem;
                        margin-bottom: 2rem;
                        
                        @include m.tablet {
                            transform: rotate(-90deg);
                        }
                        
                    }
        
                    p{
                        font-size: 6rem;
                        padding: 0;
                        margin: 0;

                        @include m.tablet {
                            font-size: 10rem;
                            transform: rotate(-90deg);
                        }
                    
                    }
                }

            }


  
        

    .menu-corto {
        display: grid;
        grid-template-rows: 30% 70%;
        position: relative;

        @include m.tablet {
            grid-template-rows: 20% 80%;
        }
        

    
   
    }
    
    .posicion-equis {
        background-color: v.$verde;
    
    }
    
    .imagen-equis {
       
        width: 4rem;
        position: absolute;
        right: 0%;
        margin-top: 3rem;
        margin-right: 3rem;

        &:hover {
            cursor: pointer;
        }

     
       
    }
    
    .nav {
        font-size: 5rem;
        text-align: right;
        margin-right: 3rem;

        
        @include m.tablet {
            font-size: 7rem;
            margin-top: 5rem;
        }
        @include m.desktop{
            font-size: 10rem;
            margin-top: 3rem;
        }
    


        a {
            color: v.$verde;
        }
    }
    


    
    .menu-secundario {

        .menu-largo {
            grid-row-start: 2;
            grid-row-end: -1;

            @include m.tablet {
                grid-row-start: 2;
                 grid-row-end: 1;
            }

        }
    }

  
