@use '../base/mixins' as m;
@use '../base/variables' as v;


.boton-wrap {

   
    .boton {

        background-color: v.$verdeOscuro;
        border: none;
        border-radius: 0.7rem;
        font-family: v.$play;
        text-transform: uppercase;
        position: relative;
        font-size: 2rem;
        font-weight: bolder;
        padding: 0.9rem 3.5rem;
        margin-top: 1rem;
        margin-bottom: 4rem;
        color: v.$blanco;

        &-q {
            display: none;
        }
    

        &:hover {
            cursor: pointer;
            background-color: v.$negro;
            color: v.$verde;
            border: 3px solid v.$verde;

        }

        @include m.desktop {
            margin-top: 4rem;
        }

    }

}