@use '../base/mixins' as m;
@use '../base/variables' as v;


.trabajo {

    &-q {
        display: none;
    }
}


.trabajo-h {

    align-items: flex-start;
    display: flex;
    justify-content: flex-start;
    padding-left: 5rem;
    gap: 1rem;
    margin-top: 15rem;
    margin-bottom: 5rem;

    svg {
        height: 3rem;
        
    }

    h2 {
        font-family: v.$play;
        color: v.$blanco;
        margin: 0 0.5rem;
        padding: 0;
        font-size: 2.8rem;
        line-height: 4.8rem;

    }

    &-q {
        display: none;
    }

 

}

.proyectos {

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    margin: 0;
    padding: 0 5rem;
    height: auto;

    @include m.tablet {
        height: auto;
    }

   


    .individual {

        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 3rem 0;
        justify-content: space-between;
        width: 100%;

        
            @include m.desktop {
                display: grid;
                grid-template-columns: 50% 50%;
                
            }
        
       

        &-imagen {

            width: 100%;
            align-items: center;
            display: flex;
            justify-content: center;

            &:hover {
                transform: scale(1.1);
            }
      

            @include m.tablet {
                width: 100%;
            
            }

            img {
                height: 22rem;
                width: 50rem;

                @include m.tablet {
                    height: 28.4rem;
                }

            }

        
        }

        &-detalle {
            width: 100%;
            color: v.$blanco;
            display: flex;
            flex-direction: column;
            justify-content: center;
            
            padding: 1rem;

            @include m.tablet {
                padding: 0 1rem;
                margin-bottom: 3rem;
                align-items: center;
            }
            
            h4 {
                margin: 0 auto;
                border: 3px solid v.$verdeOscuro;
                width: 23rem;
                height: 4.5rem;
                border-radius: 1rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    text-transform: uppercase;
                color: v.$verdeOscuro;
                margin-bottom: 1.5rem;
                margin-top: 1.5rem;
            }

            p {
                font-size: 1.7rem;
                font-family: v.$play;
                color: v.$blanco;
                margin-bottom: 1rem;
                text-align: center;

                @include m.tablet {
                    width: 50%;
                }

                @include m.desktop {
                    width: 100%;
                }
            }

          
        }
    }

}

