@use '../base/mixins' as m;
@use '../base/variables' as v;

.container {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: v.$fuente_presentacion;

    width: 100;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: rgb(20, 20, 20);
}

.neon-text{

    text-transform: uppercase;
    font-size: 10rem;
    transition:  0.1 linear;
    cursor: pointer;
    color: transparent;
    -webkit-text-stroke: 2px greenyellow;
    background-image: linear-gradient(to left, 
    greenyellow, rgb( 173, 255, 47), black, rgb(35, 31, 31));
    background-size: 200%;
    background-clip: text;

    animation: move1 5s linear 1, move2 2s linear 1 ;

    @include m.telefono {
        font-size: 20rem;
    }

    &:hover {
        color: #adff2f;
        cursor: url(../img/mano.png), auto;   
        text-shadow: 0 0 5px rgb( 173, 255, 47, .5),
                        0 0 7px rgb( 173, 255, 47, 0.5),
                        0 0 9px rgb( 173, 255, 47, 0.5),
                        0 0 15px rgb( 173, 255, 47, 0.5);
    }

}

@keyframes move2 {

    0%{
        transform: translateY(10vh);
    }
}

@keyframes move1 {

    0%{
       opacity: 0;
    }

    50% {
        
        background-position: 1000px; 
    }

   
}








 




