@use './variables' as v;

*, *:before, *:after {
    box-sizing: inherit;
}
html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    font-family: v.$fuente_textos;
    font-size: 1.6rem;
   // min-height: 100%;
   height: 100%;
    background-color: black;
}

a {
    text-decoration: none;
    font-family: v.$play ;
    font-weight: bolder;
    text-transform: uppercase;
    box-sizing: border-box;

}
img {
    max-width: 100%;
}

ul {
    list-style: none;
}

